















import Vue from 'vue'
export default Vue.extend({
  name: 'task-editor',
  props: ['createTask'],
  data: () => ({
    content: ''
  }),
  methods: {
    addTask() {
      this.createTask({
        content: this.content
      });

      this.clearForm();
    },
    clearForm() {
      this.content = '';
    }
  }
})



