














import Vue from 'vue'
import TodoService from '@/TodoService';
import _ from 'lodash';

export default Vue.extend({
  name: 'todo-task',
  props: ['task', 'deleteTask'],
  watch: {
    task: {
      deep: true,
      handler: _.debounce((newTask) => {
        TodoService.update(newTask);
      }, 250)
    }
  }
})



