






























import Vue from 'vue';
import AuthService, { ILoginData } from '@/AuthService';

export default Vue.extend({
  name: 'login',
  data: () => ({
    email: '',
    password: ''
  }),
  methods: {
    onSubmit() {
      const { email, password } = this;

      AuthService.login({ email, password });
    }
  }
});



