





































import Vue from 'vue';
import AuthService, { ISignUpData } from '@/AuthService';

export default Vue.extend({
  name: 'signup',
  data: () => ({
    email: '',
    password: '',
    name: ''
  }),
  methods: {
    onSubmit() {
      const { email, password, name } = this;

      AuthService.signup({ email, password, name });
    }
  }
});



